.slick-prev,
.slick-next {
    z-index: 999 !important;
}

.slick-prev {
    left: 45px !important;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}

.slick-next {
    right: 45px !important;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}

.coverbanner {
    background: gray;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.wapper {
    border-radius: 18px;
    border: 1px solid gray;
}

.id {
    position: relative;
    text-align: center;
}

.titleh5 {
    color: rgb(56, 54, 54);
    font-weight: bold;
}

.buttons {
    position: relative;
}

.bt {
    max-width: 175px;
    padding: 10px;
    margin: 5px;
    border: none;
    background: #bbb7a9;
    border-radius: 8px;
}

.bt:first-child {
    width: 60%;
    margin-left: 10px;
}

.bt:first-child svg {
    margin-right: 10px;
}

.bt:nth-child(2) {
    width: 5%;
}

.bt:last-child {
    width: 5%;
}

.bt:hover {
    background: rgba(0, 0, 0, 0.2);
}

.hrprofile {
    position: relative;
    padding: 5px;
}

.details {
    /* margin-left: 5px; */
    position: relative;
}

.details ul {
    list-style: none;
}

.details ul li {
    margin-bottom: 1px;
    padding-left: 10px;
}

.details ul li svg {
    color: rgb(100, 98, 98);
    padding: 4px;
}

.stories {
    display: flex;
    margin: auto;
}

.story {
    padding: 8px;
    width: 10%;
    text-align: center;
}

.story img {

    margin: auto;
    border-radius: 10px;
    box-shadow: 0px 10px 5px rgba(0, 0, 0, 0.1);
}

.introduce {
    text-align: justify;
    padding-right: 8px;
}